import React, { Component } from 'react';
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, CardMenu, IconButton } from 'react-mdl';


class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = { activeTab: 0 };
  }

  toggleCategories() {

    if(this.state.activeTab === 0){
      return(
        <div className="projects-grid">
          {/* Project 1 */}
          <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
            <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://media-exp1.licdn.com/dms/image/C4D0BAQGzuSBRRRuphw/company-logo_200_200/0?e=1590019200&v=beta&t=nLqowifK9fMtBWSYIFxltbugB4vINXHd_NaCrRK4O-s) center / cover'}} >ID:RXM0TF01KJF11WW0</CardTitle>
            <CardText class="cardtitle" >
            AWS Certified Solutions Architect - Associate -CSAA
            </CardText>
            <CardMenu style={{color: '#fff'}}>
              <IconButton name="share" />
            </CardMenu>
          </Card>

          {/* Project 2 */}
          <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
            <CardTitle   style={{color: '#fff', height: '176px', background: 'url(https://media-exp1.licdn.com/dms/image/C4E0BAQG7K2mrBP13Gg/company-logo_100_100/0?e=1590019200&v=beta&t=QCafICQTcB_udYNFXPSFGJSHExK6FPokTy1ayzesCgc) center / cover'}} ></CardTitle>
            <CardText class="cardtitle">
            IBM Blockchain Essentials
            </CardText>
            <CardMenu style={{color: '#fff'}}>
              <IconButton name="share" />
            </CardMenu>
          </Card>

          {/* Project 3 */}
          <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
            <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://media-exp1.licdn.com/dms/image/C560BAQGDnfuchAj4lw/company-logo_100_100/0?e=1590019200&v=beta&t=lW6T6ONgMTTR5l8XMdK6nxlkWwAv8AYUffNaSuDCJJY) center / cover'}} ></CardTitle>
            <CardText class="cardtitle" >
             Salesforce Platform Basics
            </CardText>
            <CardMenu style={{color: '#fff'}}>
              <IconButton name="share" />
            </CardMenu>
          </Card>
        </div>


      )
    } else if(this.state.activeTab === 1) {
      return (
        <div><h1>This is Angular</h1></div>
      )
    } else if(this.state.activeTab === 2) {
      return (
        <div><h1>This is VueJS</h1></div>
      )
    } else if(this.state.activeTab === 3) {
      return (
        <div><h1>This is MongoDB</h1></div>
      )
    }

  }



  render() {
    return(
      <div>
        <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
          <Tab>Certifications</Tab>
        </Tabs>


          <Grid>
            <Cell col={12}>
              <div className="content">{this.toggleCategories()}</div>
            </Cell>
          </Grid>


      </div>
    )
  }
}

export default Projects;



