import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';

class About extends Component {
  render() {
    return(
    
      <div>
          <Grid className="about-header-color"> 
          <Cell col={12} className="about-col">
        <h4>About Me</h4>
          <p><b>I'm Full stack developer having 9 yrs of IT Experience in developing Enterprise Application using JAVA/J2EE and Mobile Application
                using Advanced frameworks.</b>
            </p>
            <ul>
              <li>Having good Hands on experience in developing Enterprise application using JAVA/J2EE
                  frameworks like Spring MVC/Spring Boot,Hibernate and UI frameworks like Angular JS and React.
              </li>
              <li>
                 Having Proven Track record in developing Mobile application for android and ios using both native
                 and cross platform application using Apache cordova,Ionic,kotlin and RX android.
              </li>
              <li>
               Having Good knowledge in developing Micro Services using Spring Boot,AWS,Docker/Kubernates.
              </li>
              <li>
               Having Strond  knowledge on RDMS database like SQL,MYSQL,Postgres and NO SQL databases like MonogoDB,HDFS and Amazon aurora
              </li>
              <li>
               Having Strond  knowledge on Tunning SQL queries and performance Enhancements.
              </li>
              <li>
              Skilled on oracle database design,Entity framework,.Net frame work,ASP MVC,WCF,web services, and signalR.
              </li>
              <li>
              Having good knowledge on Sales force development using Lightening,Apex,visual pages.
              </li>
              <li>
              Having good knowledge on Distributed ledger Block Chain Application using Etherum and Hyper Ledger.
              </li>
              <li>
              Strong knowledge on moving on premise applications to cloud using AWS Cloud Infrastructure.
              </li>
            </ul>
          
            </Cell>
            </Grid>
      </div>
    )
  }
}

export default About;



